import React from 'react';
import PropTypes from 'prop-types';
import { Panel, Button, Group, Div, PanelHeader } from '@vkontakte/vkui';
import { PanelHeaderBack } from '@vkontakte/vkui';


const Gifts = ({ id, go, fetchedUser }) => (
    <Panel id={id}>

        <PanelHeader left={<PanelHeaderBack onClick={go} data-to="home"/>} >Подарки</PanelHeader>

        <Group title="Navigation Example">
            <Div>
                <Button size="xl" level="2" onClick={go} data-to="home">
                    Go home
                </Button>
            </Div>
        </Group>

    </Panel>
);

Gifts.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
    fetchedUser: PropTypes.shape({
        photo_200: PropTypes.string,
        first_name: PropTypes.string,
        last_name: PropTypes.string,
        city: PropTypes.shape({
            title: PropTypes.string,
        }),
    }),
};

export default Gifts;
