import React from 'react';
import PropTypes from 'prop-types';
import { Panel, Button, Group, Div, PanelHeader, View, FixedLayout, PromoBanner } from '@vkontakte/vkui';
import { PanelHeaderBack } from '@vkontakte/vkui';

const promoBannerProps = {
  title: 'Никита в дизайне',
  domain: 'vk.com',
  trackingLink: 'https://vk.com/nvdiz',
  ctaText: 'Перейти',
  advertisingLabel: 'Реклама',
  iconLink: 'https://sun9-42.userapi.com/rFD1ZdVlpd4PczPz5IV8SVmvES4ic9RqSJaVYw/iLMvhF1BnTo.jpg',
  description: 'Описание рекламы',
  ageRestrictions: "0+",
  statistics: [
    { url: '', type: 'playbackStarted' },
    { url: '', type: 'click' }
  ]
};

const Profile = ({ id, go, fetchedUser }) => (
	<Panel id={id}>

		<PanelHeader left={<PanelHeaderBack onClick={go} data-to="home"/>} >Профиль</PanelHeader>

		<Group title="Navigation Example">
			<Div>
				<Button size="xl" level="2" onClick={go} data-to="home">
					Go home
				</Button>
			</Div>
		</Group>

    <View activePanel="promo">
      <Panel id="promo">
        <FixedLayout vertical="bottom">
          <PromoBanner bannerData={promoBannerProps} />
        </FixedLayout>
      </Panel>
    </View>

	</Panel>
);

Profile.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Profile;
